<template>
  <div class="event-count full">
    <a-tabs default-active-key="1" @change="callback" card="editable-card">
      <a-tab-pane key="1" tab="今日">
        <echart :option="option" />
      </a-tab-pane>
      <a-tab-pane key="2" tab="本周">
        <echart :option="option" />
      </a-tab-pane>
      <a-tab-pane key="3" tab="本月">
        <echart :option="option" />
      </a-tab-pane>
    </a-tabs>
  </div>
</template>
<script>
import Echart from "@/components/Echart.vue";

export default {
  mounted() {
    this.$unlock();
  },
  data() {
    return {
      option: {
        title: {
          left: "center",
          textStyle: {
            //文字的样式
            color: "#ccc",
            fontSize: 16,
          },
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)",
        },
        legend: {
          // orient: "vertical",
          // left: 10,
          left: "center",
          top: "bottom",
          data: ["未处理", "处理中", "已处理"],
          itemWidth: 10,
          itemHeight: 8,
          textStyle: {
            //图例文字的样式
            color: "#ccc",
            fontSize: 12,
          },
        },
        series: [
          {
            name: "情绪占比",
            type: "pie",
            radius: ["20%", "40%"], // 40 90 ,  50  70
            center: ["50%", "30%"],
            avoidLabelOverlap: false,
            color: ["#FFDB5C", "#FF9F7F", "#65E0E3"],
            // label: {
            //   show: false,
            //   position: "center"
            // },
            emphasis: {
              label: {
                show: true,
                fontSize: "30",
                fontWeight: "bold",
              },
            },
            label: {
              normal: {
                formatter: ["{b|{b}}", "{c|{c}}"].join("\n"),
                rich: {
                  c: {
                    color: "#D1DC6B",
                    fontSize: 12,
                    fontWeight: "bold",
                    lineHeight: 3,
                  },
                  b: {
                    color: "#72CABF",
                    fontSize: 13,
                    height: 30,
                  },
                },
              },
            },
            // 引导线样式设置
            labelLine: {
              normal: {
                smooth: 0.1, //平滑
                length: 15, //引导向长度
                length2: 15, //引导线长度
              },
            },
            // labelLine: {
            //   show: false
            // },
            data: [
              { value: 335, name: "未处理" },
              { value: 234, name: "处理中" },
              { value: 475, name: "已处理" },
            ],
          },
        ],
      },
    };
  },
  methods: {
    callback(key) {
      console.log(key);
    },
  },
  components: {
    Echart,
  },
};
</script>
<style lang="less" scoped>
.event-count {
  position: relative;
  height: 100%;
  > ul {
    position: absolute;
    display: flex;

    li {
      flex: 1;
    }
  }
}
</style>
<style lang="less">
.ant-tabs {
  height: 100%;
}
.ant-tabs-content-animated{
  height: 70%;
}
.ant-tabs-bar {
  border-bottom: none;
  text-align: center;
}
.ant-tabs-tab-active {
  color: #03d3d3;
}
.ant-tabs-tab {
  color: #708acc;
}
</style>

